.cfw-side-cart-contents {
    padding: 0 1.5em 1em;
}

.cfw-cart-table:first-child {
    margin-top: 1em;
}

.cfw-cart-table {
    border-collapse: collapse;

    // Cart item table rows
    .cart-item-row:first-child {
        .cfw-cart-item-image, > th, td {
            padding-top: 0;
        }
    }
    .cart-item-row {
        border: none !important;

        > th, td {
            text-align: start;
            font-weight: normal;
            padding: 1em 0 1em 0;
            border-inline-start: none;
            border-inline-end: none;
        }

        // Product Thumb
        .cfw-cart-item-image {
            width: 3.75em;

            .cfw-cart-item-image-wrap {
                width: inherit;
                position: relative;
                height: auto;
                display: inline-block;
            }

            img {
                width: 3.75em !important;
                height: auto;
                border: 1px solid #e1e1e1;
                max-width: none;
            }

            .cfw-cart-item-quantity-bubble {
                position: absolute;
                right: -0.75em;
                top: -0.75em;
                z-index: 3;

                color: var(--cfw-cart-summary-item-quantity-text-color);
                font-size: 11.99996px;
                font-weight: 600;
                line-height: 1.75em;
                white-space: nowrap;
                text-align: center;
                border-radius: 1.75em;
                background-color: var(--cfw-cart-summary-item-quantity-background-color);
                box-sizing: border-box;
                min-width: 1.75em;
                height: 1.75em;
                padding: 0 0.58333em;
            }
        }

        // Product Title / Meta Data
        .cfw-cart-item-description {
            padding-inline-start: 1em;
            width: 100%;

            &[colspan="2"] {
                padding-inline-start: 0;
            }

            > div, > p {
                margin-bottom: 0.25rem;
            }

            .cfw-cart-item-title {
                font-weight: 400;
                font-size: 16px;

                a {
                    color: var(--cfw-body-text-color);
                    text-decoration: none;
                }

                img {
                    display: none !important;
                }
            }

            .cfw-cart-item-data {
                font-size: 0.75em;
                color: #717171;

                dd, dt {
                    margin-inline-start: 0;
                }

                dd {
                    margin-bottom: 10px;
                }

                dt {
                    font-weight: bold;
                }
            }
        }

        // Product Quantity
        .cfw-cart-item-quantity {
            white-space: nowrap;
        }

        // Product Subtotal
        .cfw-cart-item-subtotal {
            padding-inline-start: 1em;
            text-align: end;
        }
    }

    @include media-breakpoint-down(lg) {
        .cfw-cart-row .cfw-cart-item-col {
            margin-inline-start: 20px;
        }

        .cfw-cart-row {
            margin-inline-start: 0;
            margin-inline-end: 0;
            width: 100%;

            & > div:first-child {
                margin-inline-start: 0;
                width: 18%;
            }

            & > div:nth-child(2) {
                width: 70%;
            }

            & > div:last-child {
                margin-inline-end: 0;
                width: 25%;
            }

            .tax_class {
                margin-top: 4px;
                display: block;
            }
        }
    }
}